#app {
  display: flex;
  flex-direction: column;
  max-width: var(--screen-width);
  margin-left: auto;
  margin-right: auto;
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  border: 13px solid #060606;
  border-radius: 30px;

  @media screen and (max-width: 767px) {
    border: none;
    border-radius: 0;
  }

  // &::after {
  //   content: "";
  //   position: absolute;
  //   top: 3%;
  //   left: 50%;
  //   transform: translate(-50%, -50%);
  //   width: 25px;
  //   height: 25px;
  //   background-color: #060606;
  //   border-radius: 50%;
  //   @media screen and (max-width: 768px) {
  //     content: none;
  //   }
  // }
}

#screen {
  transition: opacity 1s;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.scrollable {
  overflow-y: auto;
  height: 100%;
}

.clickable {
  cursor: pointer;
  user-select: none;
}

.container {
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  position: relative;
}

.status-img {
  margin-left: auto;
  margin-right: auto;
  width: calc(100% - 80px);
}

.back-drop-filter {
  background-color: rgba(255, 255, 255, 0.5);
  -webkit-backdrop-filter: blur(20px);
  backdrop-filter: blur(20px);
}

.number-of-lines-1 {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  line-clamp: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  font-family: "SF Pro Display Medium";
}

.number-of-lines-2 {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  overflow: hidden;
  text-overflow: ellipsis;
}

input::placeholder,
textarea::placeholder {
  color: #1a712e;
}

details summary::-webkit-details-marker {
  display: none;
}

details summary::marker {
  display: none;
}

details summary {
  list-style: none;
  cursor: pointer;
}

.NoData-Found {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #1a712e;
  font-size: 26px;
  font-weight: 900;
}

.terms-and-conditions {
  margin: 20px;
}

.terms-and-conditions input {
  margin-right: 10px;
  background-color: green;
  color: 17px;
  // font-family: Arial, Helvetica, sans-serif;
}

.terms-and-conditions span a {
  color: green;
}

