  height: auto;

  .proLeftBox {
    display: flex;
    gap: 20px;

    @media screen and (max-width: 767px) {
      gap: 8px;
    }

    .proItemImgWrap {
      width: 120px;
      height: 120px;
      background-color: #fff;
      border-radius: 8px;
      display: flex;
      justify-content: center;
      align-items: center;
      overflow: hidden;

      @media screen and (max-width: 767px) {
        width: 70px;
        height: 70px;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
      }
    }

    .proItemDetailsWrap {
      display: flex;
      flex-direction: column;

      .proName {
        font-size: 16px;
        color: #1a712e;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 1;
