      .proWeigh {
        font-size: 14px;
        margin-bottom: 10px;

        @media screen and (max-width: 767px) {
          font-size: 12px;
        }
      }

      .proPrice {
        font-size: 18px;
        font-weight: 600;
        color: #1a712e;

        @media screen and (max-width: 767px) {
          font-size: 16px;
        }
      }
    }
  }

  .wishListBtn {
    position: absolute;
    top: 15px;
    right: 25px;

    @media screen and (max-width: 767px) {
      top: 8px;
      right: 8px;
    }
  }

  .lastBox {
    .cartButtonWrap {
      display: flex;
      align-items: center;
      background-color: #1a712e;
      justify-content: space-evenly;
      border-radius: 50px;
      width: 100%;
      margin: 15px 0 0 auto;
      overflow: hidden;

      .cartButton {
        padding: 8px;
        font-size: 16px;
        color: #fff;

        @media screen and (max-width: 767px) {
