          path {
            stroke: #fff;
            stroke-width: 2;
          }
        }
      }

      .countNum {
        font-size: 18px;
        font-weight: 500;
        color: #fff;

        @media screen and (max-width: 767px) {
          font-size: 14px;
        }
      }
    }

    .subscriptionButton {
      display: flex;
      align-items: center;
      background-color: #1a712e;
      justify-content: space-evenly;
      border-radius: 50px;
      width: 100%;
      margin: 15px 0 0 auto;
      overflow: hidden;
      padding: 8px;
      font-size: 16px;
      color: #fff;

      @media screen and (max-width: 767px) {
        padding: 6.5px;
        font-size: 12px;
